import axios from "axios";
import Joi from "joi";
import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
export default function Resgister() {
  const navigate = useNavigate();

  const [userData, setuserData] = useState({
    name: "",
    email: "",
    password: "",
    rePassword: "",
    phone: ""
  });
  const [isLoading, setIsLoading] = useState(false);
  const [Error, setError] = useState("");
  const [ListOfError, setListOfError] = useState([]);
  function validationRegisterForm() {
    let scheme = Joi.object({
      name: Joi.string().min(3).max(10).required(),
      phone: Joi.number().required(),
      email: Joi.string().email({
        minDomainSegments: 2,
        tlds: { allow: ["com", "net"] }
      }),
      password: Joi.string().pattern(new RegExp("^[a-zA-Z0-9]{3,30}$")),
      rePassword: Joi.string().valid(Joi.ref("password")).required()
    });
    return scheme.validate(userData, { abortEarly: false });
  }
  function getUserData(e) {
    let copyOfUserData = { ...userData };
    copyOfUserData[e.target.name] = e.target.value;
    setuserData(copyOfUserData);
  }
  async function sendUserDataToApi() {
    axios
      .post(`https://route-ecommerce.onrender.com/api/v1/auth/signup`, userData)
      .then((response) => {
        if (response.status === 201) {
          setIsLoading(false);
          navigate("/login");

          setError("");
        }
      })
      .catch((error) => {
        setError(error.response.data.message);
        setIsLoading(false);
      });

  }

  function submit(e) {
    setIsLoading(true);
    e.preventDefault();
    let validation = validationRegisterForm();
    if (validation.error) {
      setListOfError(validation.error.details);
      setError("");

      setIsLoading(false);
    } else {
      setListOfError([]);

      sendUserDataToApi();
    }
  }

  return (
    <>
      <div className="container">
        <div className="row">
          <div className="col-md-6 imgGaming   "></div>
          <div className="col-md-6 registrationForm bg-dark rounded-2 p-2">
            <p className="fs-2 fw-bold text-center">create Account</p>
            <form onSubmit={submit}>
              <div className="d-flex mt-3">
                <input
                  onChange={getUserData}
                  name="name"
                  type="text"
                  placeholder="name"
                  className="form-control fs-5   btn btn-secondary bg-dark  "
                />
              </div>
              <input
                onChange={getUserData}
                name="email"
                type="text"
                placeholder="Email"
                className="form-control fs-5 mt-4  btn btn-secondary bg-dark w-100 me-3"
              />
              <input
                onChange={getUserData}
                name="password"
                type="password"
                placeholder="Password"
                className="form-control fs-5 mt-4  btn btn-secondary bg-dark w-100 me-3"
              />
              <input
                onChange={getUserData}
                name="rePassword"
                type="password"
                placeholder="RePassword"
                className="form-control fs-5 mt-4  btn btn-secondary bg-dark w-100 me-3"
              />
              <input
                onChange={getUserData}
                name="phone"
                type="text"
                placeholder="Phone"
                className="form-control fs-5 mt-4  btn btn-secondary bg-dark w-100 me-3"
              />
              {isLoading && (
                <button className="btn border  border-secondary btn-secondary w-100 text-dark fw-bolder mt-4 mb-3 fs-3 bg-info ">
                  Loading ...{" "}
                </button>
              )}
              {!isLoading && (
                <button className="btn border  border-secondary btn-secondary w-100 text-dark fw-bolder mt-4 mb-3 fs-3 bg-info ">
                  {" "}
                  sign up{" "}
                </button>
              )}
              {ListOfError && (
                <div className="d-flex flex-column ">
                  {ListOfError.map((elm, index) => (
                    <>
                      <p
                        key={index}
                        className=" text-start ps-5 text-danger  text-start"
                      >
                        {elm.message}
                      </p>
                    </>
                  ))}
                </div>
              )}
              {Error && (
                <div className="container ">
                  <div className="row">
                    <div className="col-12">
                      <p className=" text-start ps-3 text-danger  text-start">
                        {Error}
                      </p>
                    </div>
                  </div>
                </div>
              )}
            </form>
          </div>
        </div>
      </div>
    </>
  );
}
